import { InputHandler } from "gdxjs";
import enviroment from "./enviroment";
import containerPhysical from "./containerPhysical";
import modal from "./modal";
import containerRender from "./containerRender";
import inputHandler from "./inputHandler";
import foodCreate from "./foodCreate";
import foodRender from "./foodRender";
import clearFood from "./foodClear";
import createRecipe from "./recipeCreate";
import chopicks from "./chopsticks";
import zoom from "./foodZoom";
import recipeRender from "./recipeRender";
import checkCollide from "./containerCollideFood";
import renderCollection from "./collectionRender";
import destroyFood from "./destoyFood";
import renderLoseScreen from "./renderLoseScreen";
import renderStunningScreen from "./stuningScreen";
import debugBox2d from "./debug";
import removeStunedFood from "./removeStunedFood";
export default function gamePlay(
  batch,
  worldWidth,
  worldHeight,
  whiteTex,
  camera,
  canvas,
  world,
  Image
) {
  const object = modal(worldWidth, worldHeight, Image);
  const inputHandle = new InputHandler(canvas);
  inputHandler(
    inputHandle,
    camera,
    worldWidth,
    worldHeight,
    object.container,
    object.recipes,
    canvas
  );

  return {
    drawEnviroment(bg, table) {
      enviroment(batch, worldWidth, worldHeight, bg, table);
    },
    containerPhysicals(delta) {
      containerPhysical(object.container, delta, worldWidth);
    },

    checkCollidefood() {
      checkCollide(object.container, object.recipes, world);
    },

    drawChopicks(chopstickTex, delta) {
      chopicks(
        batch,
        worldWidth,
        worldHeight,
        object.container,
        chopstickTex,
        delta
      );
    },
    // drawChopicks(chopstickTex, delta) {
    //   chopicks(
    //     batch,
    //     worldWidth,
    //     worldHeight,
    //     object.container,
    //     chopstickTex,
    //     delta
    //   );
    // },
    drawContainer(containerTex) {
      containerRender(
        batch,
        object.container,
        containerTex,
        Image.tray_shadow,
        worldHeight
      );
    },

    createFoods(delta) {
      foodCreate(
        object.foods,
        delta,
        worldWidth,
        worldHeight,
        world,
        Image.foodTex,
        object.container,
        object.recipes
      );
    },

    foodZoom(delta) {
      zoom(world, delta, worldWidth);
    },

    renderFoods() {
      foodRender(world, batch, worldHeight);
    },

    clearFoods(delta) {
      clearFood(
        world,
        worldWidth,
        worldHeight,
        Image.smoke_animation,
        delta,
        batch,
        object.container
      );
    },

    destroyFoods(delta) {
      destroyFood(world, Image.smoke_animation, delta, batch, object.container);
    },

    createRecipes() {
      createRecipe(object.recipes, object.container, Image.foodTex);
    },

    // drawChopicks(chopstickTex, delta) {
    //   chopicks(
    //     batch,
    //     worldWidth,
    //     worldHeight,
    //     object.container,
    //     chopstickTex,
    //     delta
    //   );
    // },
    renderRecipes(delta) {
      recipeRender(object.recipes, batch, Image.smoke_animation, delta);
    },

    renderCollections(delta) {
      renderCollection(
        object.container,
        batch,
        Image.smoke_animation,
        Image.food_animation,
        delta
      );
    },

    renderLoseScreens(delta) {
      renderLoseScreen(
        object.container,
        batch,
        Image,
        worldWidth,
        worldHeight,
        whiteTex,
        delta,
        object.recipes
      );
    },
    renderStunningScreens(delta) {
      renderStunningScreen(
        object.container,
        batch,
        whiteTex,
        delta,
        worldWidth,
        worldHeight
      );
    },

    debugs() {
      debugBox2d(world, batch, whiteTex);
    },

    removeStunedFoods(delta) {
      removeStunedFood(world, delta);
    }
  };
}
