let tableWidth = 0;
let tableHeight = 0;
export default async function DrawEnviroment(
  batch,
  worldWidth,
  worldHeight,
  bg,
  table
) {
  batch.setColor(1, 1, 1, 1);
  batch.draw(bg, 0, 0, worldWidth, worldHeight);

  tableWidth = worldWidth;
  tableHeight = (tableWidth * table.height) / table.width;
  batch.setColor(1, 1, 1, 1);
  batch.draw(table, 0, worldHeight - tableHeight, tableWidth, tableHeight);
}
