import { b2ShapeType } from "@flyover/box2d";

let data = 0;
let shape = 0;
const zoom = (world, delta, worldWidth) => {
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    data = body.GetUserData();
    for (
      let fixture = body.GetFixtureList();
      fixture;
      fixture = fixture.GetNext()
    ) {
      shape = fixture.GetShape();
      if (shape.GetType() === b2ShapeType.e_polygonShape) {
        data.opacity += delta * data.opacity_speed;
        if (data.opacity >= 1) {
          data.opacity = 1;
        }
        data.scaleX += delta * data.scale_speed;
        data.scaleY += delta * data.scale_speed;
        if (data.scaleX >= worldWidth / 2.7) {
          data.scaleX = worldWidth / 2.7;
          data.scaleY = worldWidth / 2.7;
        }
      }
    }
  }
};

export default zoom;
