import {
  b2BodyDef,
  b2BodyType,
  b2PolygonShape,
  b2FixtureDef,
  b2Vec2
} from "@flyover/box2d";

const createFoodBody = (
  x,
  y,
  impulseX,
  impulseY,
  density = 1,
  foods,
  world,
  image1,
  type
) => {
  const bodyDef = new b2BodyDef();
  // bodyDef.type = b2BodyType.b2_staticBody;
  bodyDef.type = b2BodyType.b2_dynamicBody;
  bodyDef.userData = {
    width: foods.width,
    height: foods.height,
    scaleX: foods.scaleX,
    scaleY: foods.scaleY,
    scale_speed: foods.scale_speed,
    stateTime: 0,
    delete_time: 0,
    image1: image1,
    goUp: true,
    type: type,
    state: "ENABLE",
    destroy_position: { x: 0, y: 0 },
    opacity: 0,
    opacity_speed: foods.opacity_speed
  };
  bodyDef.fixedRotation = true;
  bodyDef.angularDamping = Math.PI / 30;
  bodyDef.position = new b2Vec2(x, y);
  const body = world.CreateBody(bodyDef);
  const shape = new b2PolygonShape();
  shape.SetAsBox(foods.width / 2, foods.height / 2);
  const fixtureDef = new b2FixtureDef();
  fixtureDef.density = density;
  fixtureDef.shape = shape;
  fixtureDef.friction = 1;
  fixtureDef.restitution = 1;
  fixtureDef.filter.groupIndex = -1;
  body.CreateFixture(fixtureDef);
  body.ApplyLinearImpulse(
    new b2Vec2(impulseX, impulseY),
    new b2Vec2(x - foods.width / 50, y + foods.height / 10)
  );
  return body;
};

const shuffle = array => {
  array.sort(() => Math.random() - 0.5);
};

let other_food_array = [];
let random_array = [];
let tmp = [];
let tmp2 = [];

const random_food_list = (foodTex, recipes, difficulty) => {
  random_array = [];
  tmp = [];
  tmp2 = [];
  other_food_array = [];

  for (let recipe of recipes.type) {
    // if (recipe.state === "ENABLE") {
    /// optional
    random_array.push(recipe.index);
    tmp.push(recipe.index);
    // }
  }

  for (let i = 0; i < foodTex.length; i++) {
    tmp2.push(foodTex[i].type);
  }

  for (let value of tmp2) {
    if (!tmp.includes(value)) {
      other_food_array.push(value);
    }
  }
  if (difficulty >= 4 && difficulty <= 8) {
    random_array.push(
      other_food_array[
        Math.round(Math.random() * (other_food_array.length - 1))
      ]
    );
  }

  if (difficulty >= 9) {
    for (let food of other_food_array) {
      random_array.push(food);
    }
  }

  return random_array;
};

let image = 0;
let impulseY_ratio = 0;
let impulseX_ratio = 0;

let accumulate = 0;
let createTimer = 0.1;
let index_random = 0;

let random_impulseX = [];
let random_phase = 0;
const you_lose = document.getElementById("content");
const foodCreate = (
  foods,
  delta,
  worldWidth,
  worldHeight,
  world,
  foodTex,
  container,
  recipes
) => {
  accumulate += delta;
  // container.difficulty = 1;
  // console.log(container.difficulty);
  if (accumulate >= createTimer && container.gameState === "OLD_RECIPE") {
    accumulate = 0;
    you_lose.style.display = "none";
    you_lose.innerHTML = "";
    if (container.difficulty >= 1 && container.difficulty <= 3) {
      createTimer =
        Math.random() * (2 / container.difficulty) + 0.5 / container.difficulty;
    }

    if (container.difficulty >= 4 && container.difficulty <= 8) {
      createTimer =
        Math.random() * (2.5 / container.difficulty) +
        0.5 / container.difficulty;
    }

    if (container.difficulty >= 9 && container.difficulty <= 15) {
      createTimer =
        Math.random() * (2.8 / container.difficulty) +
        0.3 / container.difficulty;
    }

    if (container.difficulty >= 16) {
      createTimer = 0.1;
    }

    // createTimer = 0.3;
    // console.log(container.difficulty);
    random_impulseX = [
      -(Math.random() * (300 - 73) + 73),
      Math.random() * (300 - 73) + 73
    ];

    impulseX_ratio = random_impulseX[Math.round(Math.random() * 1 + 0)];
    impulseY_ratio = Math.random() * (12 - 11.3) + 11.3;

    random_phase = {
      x: worldWidth / impulseX_ratio,
      y: -worldHeight / impulseY_ratio
    };

    random_array = random_food_list(foodTex, recipes, container.difficulty);
    shuffle(random_array);
    index_random =
      random_array[Math.round(Math.random() * (random_array.length - 1))];

    for (let foodT of foodTex) {
      if (index_random === foodT.type) {
        image = foodT;
        break;
      }
    }
    createFoodBody(
      foods.position.x,
      foods.position.y,
      random_phase.x,
      random_phase.y,
      1,
      foods,
      world,
      image.image1,
      image.type
    );
  }
};

export default foodCreate;
