let pos = 0;
let data = 0;
const clearFood = (
  world,
  worldWidth,
  worldHeight,
  smoke_animation,
  delta,
  batch,
  container
) => {
  batch.begin();
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    pos = body.GetPosition();
    data = body.GetUserData();
    if (
      pos.x < 0 ||
      pos.x > worldWidth ||
      pos.y > worldHeight * 0.85 ||
      container.gameState === "CAN_BE_NEW" ||
      container.gameState === "LOSE" ||
      data.state === "EATED"
    ) {
      if (data.state === "ENABLE") {
        data.state = "VISIBLE";
        data.destroy_position.x = pos.x;
        data.destroy_position.y = pos.y;
      }
      if (data.state === "EATED") {
        data.state = "VISIBLE";
        // world.DestroyBody(body);
        container.gameState = "STUNNING";
      }
    }
  }
  batch.end();
};

export default clearFood;
