let chopstick = 0;
let check_all_visible = 0;

export default function chopsticks(
  batch,
  worldWidth,
  worldHeight,
  container,
  chopstickTex,
  delta
) {
  for (let i = 0; i < container.life.length; i++) {
    chopstick = container.life[i];
    if (chopstick.state === "ENABLE") {
      batch.draw(
        chopstickTex,
        worldWidth - worldWidth / 10 - container.life_width / 2,
        worldHeight / 20 +
          container.life_height * i -
          container.life_height / 2,
        container.life_width,
        container.life_height
      );
    }

    if (chopstick.state === "START_VISIBLE") {
      chopstick.stateTime += delta;
      if (chopstick.stateTime < 0.1) {
        chopstick.scale = 1.2;
      } else if (chopstick.stateTime < 0.05 && chopstick.stateTime >= 0) {
        container.life[i].position_acc += (delta * worldWidth) / 5;
      } else if (chopstick.stateTime < 0.15 && chopstick.stateTime >= 0.05) {
        container.life[i].position_acc -= (delta * worldWidth) / 5;
      } else if (chopstick.stateTime < 0.25 && chopstick.stateTime >= 0.15) {
        container.life[i].position_acc += (delta * worldWidth) / 5;
      } else if (chopstick.stateTime >= 0.25) {
        container.life[i].position_acc = 0;
        chopstick.scale -= (delta * worldWidth) / 2.5;
        if (chopstick.scale < 0) {
          chopstick.scale = 0;
        }
      }
      batch.draw(
        chopstickTex,
        worldWidth -
          worldWidth / 10 -
          (container.life_width / 2) * chopstick.scale +
          container.life[i].position_acc,
        worldHeight / 20 +
          container.life_height * i -
          (container.life_height / 2) * chopstick.scale,
        container.life_width * chopstick.scale,
        container.life_height * chopstick.scale
      );
    }
  }

  check_all_visible = container.life.filter(
    life => life.state === "START_VISIBLE"
  );
  if (
    check_all_visible.length === container.life.length &&
    container.gameState === "STUNNING"
  ) {
    container.gameState = "LOSE";
  }
}
