import { b2ShapeType } from "@flyover/box2d";

let pos = 0;
let data = 0;

const foodRender = (world, batch, worldHeight) => {
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    pos = body.GetPosition();
    data = body.GetUserData();
    for (
      let fixture = body.GetFixtureList();
      fixture;
      fixture = fixture.GetNext()
    ) {
      const shape = fixture.GetShape();

      if (shape.GetType() === b2ShapeType.e_polygonShape) {
        if (pos.y <= worldHeight / 2 && data.goUp) {
          data.goUp = false;
        }
        if (data.state === "ENABLE") {
          batch.setColor(1, 1, 1, data.opacity);
          data.image1.draw(
            batch,
            pos.x - data.width / 2,
            pos.y - data.height / 2,
            data.width,
            data.height,
            data.width / 2,
            data.height / 2,
            body.GetAngle(),
            data.scaleX,
            data.scaleY
          );
        }
      }
    }
  }
};

export default foodRender;
