import { b2ShapeType } from "@flyover/box2d";

let pos = 0;
let data = 0;
let shape = 0;
let index = 2;
let check_right = false;
let checkArray = 0;
const checkCollide = (container, recipes, world) => {
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    pos = body.GetPosition();
    data = body.GetUserData();
    for (
      let fixture = body.GetFixtureList();
      fixture;
      fixture = fixture.GetNext()
    ) {
      shape = fixture.GetShape();
      if (shape.GetType() === b2ShapeType.e_polygonShape) {
        if (
          pos.y >=
            container.position.y - container.check - container.height / 2 &&
          pos.x >= container.position.x - container.width / 2 &&
          pos.x <= container.position.x + container.width / 2 &&
          pos.y <= container.position.y + container.height / 2 &&
          data.goUp === false &&
          data.state === "ENABLE"
        ) {
          // world.DestroyBody(body);
          for (let type of recipes.type) {
            if (type.index === data.type && type.state === "ENABLE") {
              // neu dung thi delete food va add to collection
              world.DestroyBody(body);
              container.check += container.collection_height / 4;
              type.state = "VISIBLE";
              container.collection.push({
                type: type.index,
                stateTime_smoke: 0,
                stateTime_food: 0,
                state: "START_ANIMATION",
                smoke: true
              });
              check_right = true;
              break;
            } else {
              check_right = false;
            }
          }

          if (!check_right) {
            // neu sai thi ve animation red cloud
            data.state = "EATED";
            data.destroy_position.x = pos.x;
            data.destroy_position.y = pos.y;
            container.life[index].state = "START_VISIBLE";
            index--;
            if (index <= 0) {
              index = 0;
            }
          }
        }
      }
    }
  }

  checkArray = recipes.type.filter(type => type.state === "DELETED");
  if (checkArray.length === 4 && container.gameState === "CAN_BE_NEW") {
    // index = 2;
    container.difficulty += 1;
    if (container.difficulty >= 10) {
      container.difficulty = 10;
    }

    // for (let i = 0; i < 15; i++) {
    container.collectionCommon.push(container.collection);
    // }
    recipes.type.length = 0;
    container.gameState = "NEW_RECIPE"; // nếu chơi nhiều phase thì dùng state này
    // container.gameState = "VICTORY"; // nếu thành công 1 lần thì win thì dùng state này
    container.collection = []; // nếu thành công 1 lần thì win thì không cần
    container.check = 0;
    // console.log(container.collectionCommon);
  }

  if (container.gameState === "LOSE") {
    container.difficulty = 1;
    index = 2;
    container.check = 0;
    for (let type of recipes.type) {
      type.state = "VISIBLE";
    }
  }
};

export default checkCollide;
