import "./index.css";
import {
  resizeCanvas,
  createOrthoCamera,
  createBatch,
  createGameLoop
} from "gdxjs";
import gamePlay from "./gamePlay";
import loadImage from "./loadImage";
import createWhiteTexture from "gl-white-texture";
import { b2World, b2Vec2 } from "@flyover/box2d";
const init = async () => {
  const canvas = document.getElementById("main");
  const infor = document.getElementById("infor");
  const [width, height] = resizeCanvas(canvas);
  // const [width, height] = [canvas.width, canvas.height];
  const gl = canvas.getContext("webgl");
  const worldWidth = 10;
  const worldHeight = (worldWidth * height) / width;
  const cam = createOrthoCamera(worldWidth, worldHeight, width, height);
  const batch = createBatch(gl);
  const whiteTex = createWhiteTexture(gl);
  const world = new b2World(new b2Vec2(0, 10));

  const image = await loadImage(gl);

  const newGame = gamePlay(
    batch,
    worldWidth,
    worldHeight,
    whiteTex,
    cam,
    canvas,
    world,
    image
  );

  gl.clearColor(0, 0, 0, 1);
  const update = delta => {
    gl.clear(gl.COLOR_BUFFER_BIT);
    batch.setProjection(cam.combined);
    world.Step(delta, 8, 3);
    newGame.containerPhysicals(delta);
    newGame.createRecipes();
    newGame.createFoods(delta);
    newGame.removeStunedFoods(delta);
    newGame.clearFoods(delta);
    newGame.checkCollidefood();
    newGame.foodZoom(delta);
    batch.begin();
    newGame.drawEnviroment(image.bg, image.table);
    newGame.drawChopicks(image.chopsticks, delta);
    newGame.drawContainer(image.container);
    newGame.drawChopicks(image.chopsticks, delta);
    newGame.renderFoods();
    newGame.renderRecipes(delta);
    newGame.renderCollections(delta);
    newGame.destroyFoods(delta);
    // newGame.renderEndScreens(delta);
    newGame.renderLoseScreens(delta);
    newGame.renderStunningScreens(delta);
    // newGame.debugs();
    batch.end();
  };

  const game = createGameLoop(update);

  setInterval(() => {
    infor.innerHTML = `FPS: ${Math.round(game.getFps())}`;
  }, 1000);
};

init();
