import { fillRect } from "gdxjs";

let image_food = 0;
let width = 0;
let height = 0;
let width_board = 0;
let height_board = 0;
let scale_speed = 4;
// let result_speed = 3.5;
let board_max_size = 0;
let scale = 0;
let offSetX = 0;
let offSetY = 0;
let scale_x = 0;
let stateTime = new Array(20).fill(0);
let check_render_board = false;
const you_lose = document.getElementById("content");
const renderEndScreen = (
  container,
  batch,
  image,
  worldWidth,
  worldHeight,
  whiteTex,
  delta,
  recipes
) => {
  if (
    container.gameState === "LOSE_SCREEN" ||
    container.gameState === "CAN_RESET_LOSE"
  ) {
    let length = container.collectionCommon.length;
    container.position.set(worldWidth / 2, (worldHeight * 3.1) / 4);
    batch.setColor(0, 0, 0, 0.3);
    fillRect(batch, whiteTex, 0, 0, worldWidth, worldHeight);

    batch.setColor(1, 1, 1, 1);
    // draw victory board
    board_max_size = worldWidth / 1;

    if (width_board < board_max_size) {
      width_board += (worldWidth / 2) * delta * scale_speed;
      height_board +=
        (((worldWidth / 2) * image.win_board.height) / image.win_board.width) *
        delta *
        scale_speed;
    } else {
      check_render_board = true;
      width_board = board_max_size;
      height_board =
        (board_max_size * image.win_board.height) / image.win_board.width;
      if (length === 0) {
        you_lose.style.display = "block";
        you_lose.innerHTML = "<h2>(Tap To Play)</h2>";
        container.gameState = "CAN_RESET_LOSE";
        you_lose.style.top = "50%";
      } else {
        you_lose.innerHTML = "<h2>(Tap To Play)</h2>";
        you_lose.style.display = "block";
        you_lose.style.top = "70%";
      }
    }

    let broadX = worldWidth / 2 - width_board / 2;
    let broadY = worldHeight / 3 - height_board / 2;
    batch.draw(image.win_board, broadX, broadY, width_board, height_board);

    // draw collection
    let containerY = worldHeight / 2;

    scale = 0.7;
    if (check_render_board) {
      for (let j = 0; j < container.collectionCommon.length; j++) {
        stateTime[j] += delta * recipes.width * 4;
        if (stateTime[j] >= 1) {
          stateTime[j] = 1;
          container.gameState = "CAN_RESET_LOSE";
        }
        width = recipes.width / 1.9;
        height = (image.container.height * width) / image.container.width;
        if (j >= 0 && j <= 4) {
          offSetY = height * 4.8;
          scale_x = j + 1 - 1;
        }
        if (j >= 5 && j <= 9) {
          offSetY = height / 1.8;
          scale_x = j + 1 - 6;
        }
        if (j >= 10 && j <= 14) {
          offSetY = -height * 3.6;
          scale_x = j + 1 - 11;
        }
        offSetX = width * 1.1 * scale_x;

        batch.setColor(1, 1, 1, stateTime[j]);
        batch.draw(
          image.container,
          worldWidth / 2 - width * 2.7 + offSetX,
          containerY - height / 2 - offSetY,
          width,
          height
        );
        // console.log(container.collectionCommon);
        for (let i = 0; i < container.collectionCommon[j].length; i++) {
          for (let foodTex of image.foodTex) {
            if (foodTex.type === container.collectionCommon[j][i].type) {
              image_food = foodTex.image1;
            }
            if (foodTex.type === 1) {
              width = recipes.width / 1.9;
              height = (foodTex.image1.height * width) / foodTex.image1.width;
            }
          }
          batch.setColor(1, 1, 1, stateTime[j]);
          image_food.draw(
            batch,
            worldWidth / 2 - width * 2.7 + (width * scale) / 4 + offSetX,
            containerY -
              (height * scale) / 2 -
              height / 3 -
              offSetY -
              (height * scale * i) / 1.6,
            width * scale,
            height * scale
          );
        }
      }
    }
  } else {
    width = 0;
    height = 0;
    width_board = 0;
    height_board = 0;
    check_render_board = false;
    stateTime = new Array(20).fill(0);
  }
};

export default renderEndScreen;
