import { Vector2 } from "gdxjs";

const worldPosition = new Vector2(0, 0);
const screenPosition = new Vector2(0, 0);

const DEVICE_PIXEL_RATIO = window.devicePixelRatio || 1;

export default function inputHandler(
  inputHandler,
  camera,
  worldWidth,
  worldHeight,
  container,
  recipes,
  canvas
) {
  // canvas.addEventListener("mousedown", e => {
  //   const rect = canvas.getBoundingClientRect();
  //   console.log(e.clientX - rect.left, e.clientY - rect.top);
  // });

  inputHandler.addEventListener("touchStart", (x, y) => {
    x = x * DEVICE_PIXEL_RATIO;
    y = y * DEVICE_PIXEL_RATIO;
    screenPosition.x = x;
    screenPosition.y = y;
    camera.unprojectVector2(worldPosition, screenPosition);
    container.target.x = worldPosition.x;

    if (worldPosition.x >= worldWidth / 2) {
      container.moveRight = true;
      container.inRight = true;
    } else {
      container.moveLeft = true;
      container.inLeft = true;
    }
    inputHandler.addEventListener("touchEnd", () => {
      if (container.inLeft) {
        container.moveLeft = false;
        container.inLeft = false;
      } else {
        container.moveRight = false;
        container.inRight = false;
      }
    });

    if (
      worldPosition.x >= 0 &&
      worldPosition.x <= worldWidth &&
      worldPosition.y >= 0 &&
      worldPosition.y <= worldHeight
    ) {
      if (container.gameState === "CAN_RESET_LOSE") {
        recipes.type.length = 0;
        container.collectionCommon.length = 0;
        container.collection.length = 0;
        container.life = [
          { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
          { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
          { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 }
        ];
        container.gameState = "NEW_RECIPE";
      }
    }
  });

  document.addEventListener("mouseup", e => {
    if (container.gameState === "CAN_RESET_LOSE") {
      recipes.type.length = 0;
      container.collectionCommon.length = 0;
      container.collection.length = 0;
      container.life = [
        { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
        { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
        { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 }
      ];
      container.gameState = "NEW_RECIPE";
    }
  });

  document.addEventListener("keydown", e => {
    if (e.which === 39) {
      container.moveRight = true;
    }
    if (e.which === 37) {
      container.moveLeft = true;
    }
  });

  document.addEventListener("keyup", e => {
    if (e.which === 39) {
      container.moveRight = false;
    }
    if (e.which === 37) {
      container.moveLeft = false;
    }
  });
}
