import { b2ShapeType } from "@flyover/box2d";

let data = 0;
let shape = 0;
let scale_ratio = 2;
const destroyFood = (world, smoke, delta, batch, container) => {
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    data = body.GetUserData();

    for (
      let fixture = body.GetFixtureList();
      fixture;
      fixture = fixture.GetNext()
    ) {
      shape = fixture.GetShape();
      if (shape.GetType() === b2ShapeType.e_polygonShape) {
        if (data.state === "VISIBLE") {
          data.stateTime += delta;
          batch.setColor(1, 0, 0, 1);
          smoke.animation
            .getKeyFrame(data.stateTime, 1)
            .draw(
              batch,
              data.destroy_position.x -
                (data.width * data.scaleX * scale_ratio) / 2 +
                data.width / 2,
              data.destroy_position.y -
                (data.height * data.scaleY * scale_ratio) / 2,
              data.width * data.scaleX * scale_ratio,
              data.height * data.scaleY * scale_ratio
            );

          if (data.stateTime >= smoke.frame_length * smoke.duration) {
            world.DestroyBody(body);
            // if (container.gameState === "CAN_BE_NEW") {
            //   container.gameState = "CREATE_RECIPE";
            // }
          }
        }
      }
    }
  }
};

export default destroyFood;
