let type = 0;
let position = 0;
let scale = 3;
const recipeRender = (recipes, batch, smoke, delta) => {
  if (recipes.type.length > 0) {
    for (let i = 0; i < recipes.type.length; i++) {
      type = recipes.type[i];
      position = recipes.position[i];
      if (type.state === "ENABLE") {
        batch.setColor(1, 1, 1, 1);
        type.image.draw(
          batch,
          position.x,
          position.y - type.height,
          type.width,
          type.height
        );
      }
      if (type.state === "VISIBLE") {
        type.stateTime += delta;
        batch.setColor(1, 1, 1, 1);
        smoke.animation
          .getKeyFrame(type.stateTime, 1)
          .draw(
            batch,
            position.x - (type.width / 4) * scale,
            position.y - (type.height / 1.7) * scale,
            type.width * scale,
            type.height * scale
          );
        if (type.stateTime >= smoke.duration * smoke.frame_length) {
          type.state = "DELETED";
        }
      }
    }
  }
};
export default recipeRender;
