import { Vector2 } from "gdxjs";
export default function modal(worldWidth, worldHeight, image) {
  const containerWidth = worldWidth / 4;
  const containerHeight =
    (containerWidth * image.container.height) / image.container.width;

  const FOOD_WIDTH_1 = worldWidth / 20;
  const FOOD_HEIGHT_1 =
    (FOOD_WIDTH_1 * image.foodTex[1].image1.height) /
    image.foodTex[1].image1.width;

  const scaleX = 1;
  const scaleY = 1;
  const scale_speed = worldWidth / 11;
  const opacity_speed = worldWidth / 20;

  const RECIPE_WIDTH = worldWidth / 7;
  const RECIPE_HEIGHT =
    (RECIPE_WIDTH * image.foodTex[1].image1.height) /
    image.foodTex[1].image1.width;

  const COLLECTION_WIDTH = worldWidth / 3;
  const COLLECTION_HEIGHT = (COLLECTION_WIDTH * 255) / 400;

  const LIFE_WIDTH = worldWidth / 8;
  const LIFE_HEIGHT =
    (LIFE_WIDTH * image.chopsticks.height) / image.chopsticks.width;

  const distance = RECIPE_WIDTH + worldWidth / 150;
  // const lifeWidth = worldWidth / 10;
  // const lifeHeight =
  //   (lifeWidth * image.chopsticks.height) / image.chopsticks.width;
  const foods = {
    position: new Vector2(worldWidth / 2, worldHeight / 1.4),
    scale: worldWidth / 10,
    speed: Math.PI / 100,
    width: FOOD_WIDTH_1,
    height: FOOD_HEIGHT_1,
    scaleX: scaleX,
    scaleY: scaleY,
    scale_speed: scale_speed,
    opacity_speed: opacity_speed
  };

  const recipes = {
    type: [],
    position: [
      new Vector2(worldWidth / 2.95, worldHeight / 1.038),
      new Vector2(worldWidth / 2.95 + distance, worldHeight / 1.038),
      new Vector2(worldWidth / 2.95 + distance * 2, worldHeight / 1.038),
      new Vector2(worldWidth / 2.95 + distance * 3, worldHeight / 1.038)
    ],
    width: RECIPE_WIDTH,
    height: RECIPE_HEIGHT
  };

  const container = {
    position: new Vector2(worldWidth / 2, (worldHeight * 3.1) / 4),
    target: new Vector2(worldWidth / 2, (worldHeight * 3.1) / 4),
    difficulty: 1,
    check: 0,
    width: containerWidth,
    height: containerHeight,
    speed: worldWidth * 1.5,
    collection: [],
    collection_width: COLLECTION_WIDTH,
    collection_height: COLLECTION_HEIGHT,
    gameState: "NEW_RECIPE",
    moveLeft: false,
    moveRight: false,
    moveToLeft: false,
    moveToRight: false,
    life: [
      { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
      { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 },
      { state: "ENABLE", stateTime: 0, position_acc: 0, scale: 1 }
    ],
    life_width: LIFE_WIDTH,
    life_height: LIFE_HEIGHT,
    stun_time: 0.5,
    collectionCommon: []
  };
  return {
    foods,
    container,
    recipes
  };
}
