let food = 0;
let animation = 0;
let width = 0;
let height = 0;
let scale_smoke = 1.6;

const renderCollection = (container, batch, smoke, food_animation, delta) => {
  width = container.collection_width;
  height = container.collection_height;
  if (container.collection.length > 0) {
    for (let i = 0; i < container.collection.length; i++) {
      food = container.collection[i];
      for (let animations of food_animation) {
        if (animations.type === food.type) {
          animation = animations;
        }
      }
      if (food.state === "START_ANIMATION") {
        food.stateTime_smoke += delta;
        if (
          food.stateTime_smoke >=
          (smoke.frame_length / 1.5) * smoke.duration
        ) {
          food.stateTime_food += delta;

          animation.animation
            .getKeyFrame(food.stateTime_food, 1)
            .draw(
              batch,
              container.position.x - width / 2,
              container.position.y - height - (height * i) / 4,
              width,
              height
            );

          if (food.stateTime_food >= animation.length * animation.duration) {
            food.stateTime_food = (animation.length + 1) * animation.duration;
            // if (i === 3 && container.gameState === "OLD_RECIPE") {
            //   container.gameState = "CAN_BE_NEW";
            // }
          }
        }
        if (food.stateTime_smoke >= smoke.frame_length * smoke.duration) {
          food.smoke = false;
        }
        if (food.smoke) {
          if (i === 3 && container.gameState === "OLD_RECIPE") {
            container.gameState = "CAN_BE_NEW";
          }
          smoke.animation
            .getKeyFrame(food.stateTime_smoke, 1)
            .draw(
              batch,
              container.position.x - (width / 2.4) * scale_smoke,
              container.position.y - height - (height * i) / 4,
              width * scale_smoke,
              height * scale_smoke
            );
        }
      }
    }
  }
};

export default renderCollection;
