import { drawCircle, drawPolygon } from "gdxjs";
import { b2ShapeType, b2Vec2 } from "@flyover/box2d";
const tmp = [];
const worldPoint = new b2Vec2(0, 0);

const debugBox2d = (world, batch, whiteTex) => {
  for (let body = world.GetBodyList(); body; body = body.GetNext()) {
    const pos = body.GetPosition();
    for (
      let fixture = body.GetFixtureList();
      fixture;
      fixture = fixture.GetNext()
    ) {
      const shape = fixture.GetShape();
      if (shape.GetType() === b2ShapeType.e_polygonShape) {
        const vertices = shape.m_vertices;
        tmp.length = 0;
        for (let vertice of vertices) {
          body.GetWorldPoint(vertice, worldPoint);
          tmp.push(worldPoint.x, worldPoint.y);
        }
        drawPolygon(batch, whiteTex, tmp, 0.05, 0, 0, 0);
      } else if (shape.GetType() === b2ShapeType.e_circleShape) {
        drawCircle(
          batch,
          whiteTex,
          pos.x,
          pos.y,
          shape.m_radius,
          0.05,
          10,
          0,
          0,
          0
        );
      }
    }
  }
};

export default debugBox2d;
