import { fillRect } from "gdxjs";
let scale = 0;
export default function containerRender(
  batch,
  container,
  containerTex,
  shadowTex,
  worldHeight
) {
  batch.setColor(1, 1, 1, 1);
  fillRect(
    batch,
    containerTex,
    container.position.x - container.width / 2,
    container.position.y - container.height / 2,
    container.width,
    container.height
  );
  // console.log(container.width);
  // console.log(container.height);
  scale = 0.7;
  batch.setColor(1, 1, 1, 1);
  fillRect(
    batch,
    shadowTex,
    container.position.x - (container.width / 2) * scale,
    container.position.y - (container.height / 2) * scale + worldHeight / 12,
    container.width * scale,
    container.height * scale
  );
  // fillRect(
  //   batch,
  //   dishTex,
  //   container.position.x - container.width / 2.5,
  //   dish.position.y - dish.height / 2,
  //   dish.width,
  //   dish.height
  // );
}
