const shuffle = array => {
  array.sort(() => Math.random() - 0.5);
};

const createRecipe = (recipes, container, foodTex) => {
  if (container.gameState === "NEW_RECIPE") {
    shuffle(foodTex);
    recipes.type.length = 0;
    for (let i = 0; i < 4; i++) {
      recipes.type.push({
        index: foodTex[i].type,
        image: foodTex[i].image1,
        state: "ENABLE",
        stateTime: 0,
        height: recipes.height,
        // (recipes.width * foodTex[1].image1.height) / foodTex[1].image1.width,
        width: recipes.width
      });
    }
    container.gameState = "OLD_RECIPE";
  }
};

export default createRecipe;
