import { fillRect } from "gdxjs";

let speed = 3;
let tmp = 0;
let index = 0;
let accumulate = [0, 0, 0];
const renderStunningScreen = (
  container,
  batch,
  whiteTex,
  delta,
  worldWidth,
  worldHeight
) => {
  if (container.gameState === "STUNNING" || container.gameState === "LOSE") {
    accumulate[index] += delta * speed;
    tmp = 1 - accumulate[index];
    if (tmp <= 0) {
      tmp = 0;
    }
    batch.setColor(1, 0, 0, tmp);
    fillRect(batch, whiteTex, 0, 0, worldWidth, worldHeight);
    if (tmp <= 0) {
      if (container.gameState === "LOSE") {
        container.gameState = "LOSE_SCREEN";
        accumulate = [0, 0, 0];
        index = 0;
      }
      if (container.gameState !== "LOSE_SCREEN") {
        container.gameState = "OLD_RECIPE";
        index++;
      }
    }
  }
  if (container.gameState === "NEW_RECIPE") {
    accumulate = [0, 0, 0];
    index = 0;
  }
};

export default renderStunningScreen;
