export default function containerPhysical(container, delta, worldWidth) {
  if (
    (container.moveLeft && container.gameState === "OLD_RECIPE") ||
    (container.moveLeft && container.gameState === "STUNNING")
  ) {
    container.position.x -= container.speed * delta;
    if (container.position.x <= container.width / 2) {
      container.position.x = container.width / 2;
    }
  }

  if (
    (container.moveRight && container.gameState === "OLD_RECIPE") ||
    (container.moveRight && container.gameState === "STUNNING")
  ) {
    container.position.x += container.speed * delta;
    if (container.position.x >= worldWidth - container.width / 2) {
      container.position.x = worldWidth - container.width / 2;
    }
  }
}
